const inConstructionEl = document.querySelector('.inconstruction')

window.setInterval(() => {
  switch (inConstructionEl.textContent) {
    case 'Website in construction':
      inConstructionEl.textContent = 'Website in construction.'
      break
    case 'Website in construction.':
      inConstructionEl.textContent = 'Website in construction..'
      break
    case 'Website in construction..':
      inConstructionEl.textContent = 'Website in construction...'
      break
    case 'Website in construction...':
      inConstructionEl.textContent = 'Website in construction'
      break
  }
}, 300)
